h1 {
  letter-spacing: -0.04em; }
  h1 * {
    letter-spacing: initial; }

:root {
  font-size: 62.5%; }

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0;
  font-weight: 400; }

h2 {
  font-size: 36px;
  line-height: 42px; }

h2 {
  font-size: 24px;
  line-height: 28px;
  font-weight: 600; }

h3 {
  font-size: 24px;
  line-height: 28px;
  font-weight: 600; }

ul {
  padding: 0;
  margin: 0; }
  ul li {
    list-style: none; }

img {
  width: 100%;
  max-width: 100%;
  height: auto; }

a {
  transition: all 0.3s;
  text-decoration: none;
  color: #FFFFFF; }
  a:hover {
    text-decoration: none; }

button {
  cursor: pointer;
  outline: none !important;
  border: none;
  background: transparent; }

svg,
path,
circle {
  transition: all 0.3s; }

input {
  outline: none;
  border: none;
  box-shadow: none; }
  input:focus {
    outline: none; }
  input::placeholder {
    color: #C4C4C4;
    font-size: 16px;
    line-height: 19px; }

img {
  width: auto;
  max-width: 100%;
  height: auto; }

html,
body {
  max-width: 100%;
  overflow-x: hidden; }

body {
  font-size: 16px;
  font-family: "gilroyregular", sans-serif;
  color: #000;
  font-size: 16px;
  line-height: 19px;
  font-weight: 400; }

.outer-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between; }

.container {
  margin: 0 auto; }

@media (min-width: 1200px) {
  .container {
    max-width: 1400px;
    width: 95%;
    padding-right: 15px;
    padding-left: 15px; } }

@media (max-width: 991px) {
  .container {
    max-width: 100%;
    padding-right: 30px;
    padding-left: 30px; } }

@media (max-width: 767px) {
  .container {
    padding-right: 20px;
    padding-left: 20px; } }

